<style>
.signin-box_b .form-control:focus {
    color: #495057;
    background-color: #fff;
    border-color: #80bdff;
    outline: 0;
    box-shadow: 0 1px 0 #378ef0;
}


.sub-text-ac h5 {
    font-size: 16px;
    color: #161B1F;
    font-weight: 400;
}
.sub-text-ac a{
  color: #007bff;
}
.sub-text-ac h4{
  font-size: 36px;
    margin-bottom: 0;
    color: #161B1F;
}

.join-workshire_b label {
    margin-bottom: 0px !important;
    position: relative;
    top: 10px;
    font-size: 12px;
    color: #747474;
}
.join-workshire_b .form-control {
    border: 0;
    border-bottom: 0.33px solid rgba(0, 0, 0, 0.15);
    border-radius: 0px;
    font-size: 14px;
}

.trms-condition p{
  margin-bottom: 0px;
}
.trms-condition{
  margin-top: 15px;
}
.trms-condition p, .trms-condition ul li{
font-size: 13px;
    margin: 0;
    padding: 0;
    display: flow-root;
    line-height: 20px;
  }
  .trms-condition ul li a{
    line-height: 20px;
  }
  .trms-condition ul li{

  }
  .form-group select{
   -webkit-appearance: none;
  -moz-appearance: none;
  background: transparent;
    background: url(http://cdn1.iconfinder.com/data/icons/cc_mono_icon_set/blacks/16x16/br_down.png) no-repeat right ;
  background-repeat: no-repeat;
  background-position-x: 100%;
  background-position-y: 5px;
  border: 1px solid #dfdfdf;
  border-radius: 2px;
  margin-right: 2rem;
 
  }
  .date-of-birth p {
    margin-bottom: 0px;
    font-size: 13px;
}
.Signup__step {
    color: #747474;
}
.Signup__step span {
    color: #747474;
}
.Signup__step strong {
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    font-family: "DM Sans";
    color: #6B7177;
}
</style>
<template>
	<section id="signin" class=" d-flex align-items-center">

    <div class="container-xl">
      <div class="row">
        <div class="col-lg-6 d-flex flex-column justify-content-center">
          <div class="signin-lgo">
         <img src="../assets/images/workwire.png">
       </div>
   </div>

           <div class="col-lg-6 d-flex  justify-content-center align-items-center pt-4 ">

           <div class="signin-box_b">

            <div class="sub-text-ac">
            <strong class="Signup__step">Step <span>2</span> of <span>2</span></strong>
            <h4>Create an account</h4>
            <h5>Already have an account? <a href="#"> Sign in </a></h5>
            <div class="join-workshire_b">
              <form v-on:submit.prevent="onSubmit" method="post" class="body">
                <div class="row">
                  <div class="col-md-6">
                 <div class="form-group">
                  <label>First name</label>
            <input type="text" name="firstName" class="form-control" placeholder="" v-model="posts.firstName">
             </div>
           </div>
              <div class="col-md-6">
                 <div class="form-group">
                   <label>Last name</label>
            <input type="text" name="lastName" class="form-control" placeholder="" v-model="posts.lastName">
             </div>
           </div>

<div class="col-md-12 date-of-birth">
  <p>Date of birth</p>
</div>
           
              <div class="col-md-8 pr-2">
                 <div class="form-group">
    <label for="exampleFormControlSelect1">Month</label>
    <select class="form-control" id="exampleFormControlSelect1" v-model="posts.months">
      <option>1</option>
      <option>2</option>
      <option>3</option>
      <option>4</option>
      <option>5</option>
    </select>
  </div>
           </div>
              <div class="col-md-4 pr-2">
                <div class="form-group">
                  <label>Year</label>
            <input type="text" name="" class="form-control" placeholder="" v-model="posts.year">
             </div>
           </div>

             <div class="col-md-6 pr-2">
                <div class="form-group d-flex">
    <label for="exampleFormControlSelect1">Country/Region</label>
    <select class="form-control" id="exampleFormControlSelect1" v-model="posts.country">
      <option>India</option>
      <option>USA</option>
      <option>Iceland</option>
      <option>Iraq</option>
      <option>Ireland</option>
    </select>
  </div>
           </div>

           </div>
            
            <div class="trms-condition">
             <p>By clicking Create account, I agree that:</p>
             <ul>
              <li>&#x2022; I have read and accepted the <a class="spectrum-Link" href="#" rel="noopener noreferrer" target="_blank">Terms of Use</a>.</li>
              <li>&#x2022; The <a class="te-link" href="#" target="_blank">workshires family of companies</a> may keep me informed with <a class="te-link" href="#"  target="_blank">personalized</a> emails about products and services.</li></ul>
            </div>

            <div class="box-join_b">
            <button  type="submit" class="join-signin_b">DONE <img src="../assets/images/Group.png"></button>
            
         </div>
         </form>

</div>



</div>


           </div>


            </div>
          
           </div>


        <!-- <div class="col-lg-6 order-1 order-lg-2 hero-img" >
          <img src="image/sliderbg.png" class="img-fluid " alt="">
        </div> -->
      </div>
    </div>

  </section>
</template>
<script>
import { putRequest } from './ApiFunctions/index';

export default {
  name: 'Signupformstep2',
  data() {
        return {
            posts: {
                firstName:"",
                lastName:"",
                dob:"",
                months: '',
                country: '',
                year: ''
            },
            errorMsg:"",
            emailRequired:"",
            passwordRequired:"",
            SuccessMsg:""
        }
    },
    methods: {
        async onSubmit() {
           //alert("hello");
            console.log("posts",this.posts.birth_year);
            var register = await putRequest('/update-user/'+this.$route.params.id, this.posts);
             console.log("posts",register);
             this.$router.push('/sign-up-step3/'+register.data._id);
            


        },
    }

}
</script>




